/*Style Scroll*/
.h__items-prod {
  overflow-y: auto;
  max-height: 400px; }

.mCustomScrollbar {
  -ms-touch-action: pinch-zoom;
  touch-action: pinch-zoom;
  &.mCS_no_scrollbar, &.mCS_touch_action {
    -ms-touch-action: auto;
    touch-action: auto; } }

.mCustomScrollBox {
  position: relative;
  overflow: hidden;
  height: 100%;
  max-width: 100%;
  outline: none;
  direction: ltr; }

.mCSB_container {
  overflow: hidden;
  width: auto;
  height: auto; }

.mCSB_inside > .mCSB_container {}

.mCSB_container.mCS_no_scrollbar_y.mCS_y_hidden {
  margin-right: 0; }

.mCS-dir-rtl > .mCSB_inside > .mCSB_container {
  margin-right: 0;
  margin-left: 30px;
  &.mCS_no_scrollbar_y.mCS_y_hidden {
    margin-left: 0; } }

.mCSB_scrollTools {
  position: absolute;
  width: 12px;
  height: auto;
  left: auto;
  top: 0;
  right: -1px;
  bottom: 0; }

.mCSB_outside + .mCSB_scrollTools {
  right: -26px; }

.mCS-dir-rtl > {
  .mCSB_inside > .mCSB_scrollTools {
    right: auto;
    left: 0; }
  .mCSB_outside + .mCSB_scrollTools {
    right: auto;
    left: 0;
    left: -26px; } }

.mCSB_scrollTools {
  .mCSB_draggerContainer {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: auto; }
  a + .mCSB_draggerContainer {
    margin: 20px 0; }
  .mCSB_draggerRail {
    width: 13px;
    height: 100%;
    margin: 0 auto; }
  .mCSB_dragger {
    cursor: pointer;
    width: 100%;
    height: 52px;
    z-index: 1;
    .mCSB_dragger_bar {
      position: relative;
      width: 12px;
      height: 100%;
      margin: 0 auto;
      text-align: center;
      background-color: #000; } } }

.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand {
  .mCSB_dragger.mCSB_dragger_onDrag_expanded .mCSB_dragger_bar, .mCSB_draggerContainer:hover .mCSB_dragger .mCSB_dragger_bar {
    width: 12px; }
  .mCSB_dragger.mCSB_dragger_onDrag_expanded + .mCSB_draggerRail, .mCSB_draggerContainer:hover .mCSB_draggerRail {
    width: 8px; } }

.mCSB_scrollTools {
  .mCSB_buttonUp {
    display: block;
    position: absolute;
    height: 20px;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    cursor: pointer; }
  .mCSB_buttonDown {
    display: block;
    position: absolute;
    height: 20px;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    cursor: pointer;
    bottom: 0; } }

.mCSB_container_wrapper {
  position: absolute;
  height: auto;
  width: auto;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin-right: 30px;
  margin-bottom: 30px;
  > .mCSB_container {
    padding-right: 30px;
    padding-bottom: 30px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; } }

.mCSB_vertical_horizontal > .mCSB_scrollTools {
  &.mCSB_scrollTools_vertical {
    bottom: 20px; }
  &.mCSB_scrollTools_horizontal {
    right: 20px; } }

.mCSB_container_wrapper {
  &.mCS_no_scrollbar_x.mCS_x_hidden + .mCSB_scrollTools.mCSB_scrollTools_vertical {
    bottom: 0; }
  &.mCS_no_scrollbar_y.mCS_y_hidden + .mCSB_scrollTools ~ .mCSB_scrollTools.mCSB_scrollTools_horizontal {
    right: 0; } }

.mCS-dir-rtl > {
  .mCustomScrollBox.mCSB_vertical_horizontal.mCSB_inside > {
    .mCSB_scrollTools.mCSB_scrollTools_horizontal {
      right: 0;
      left: 20px; }
    .mCSB_container_wrapper.mCS_no_scrollbar_y.mCS_y_hidden + .mCSB_scrollTools ~ .mCSB_scrollTools.mCSB_scrollTools_horizontal {
      left: 0; } }
  .mCSB_inside > .mCSB_container_wrapper {
    margin-right: 0;
    margin-left: 30px; } }

.mCSB_container_wrapper {
  &.mCS_no_scrollbar_y.mCS_y_hidden > .mCSB_container {
    padding-right: 0; }
  &.mCS_no_scrollbar_x.mCS_x_hidden > .mCSB_container {
    padding-bottom: 0; } }

.mCustomScrollBox.mCSB_vertical_horizontal.mCSB_inside > .mCSB_container_wrapper {
  &.mCS_no_scrollbar_y.mCS_y_hidden {
    margin-right: 0;
    margin-left: 0; }
  &.mCS_no_scrollbar_x.mCS_x_hidden {
    margin-bottom: 0; } }

.mCSB_scrollTools {
  .mCSB_draggerRail {
    background-color: #e0e0e0; } }
