/* Reset Style */
@charset "utf-8";

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, address, em, img, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, nav, section, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

ol, ul {
  list-style: none; }

img {
  vertical-align: top;
  max-width: 100%;
  width: auto;
  height: auto; }

input[type="text"]:focus {
  outline: none; }

button, input[type="submit"] {
  border: 0; }

button, input {
  line-height: normal;
  outline: none; }

button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
  font-family: 'RobotoCondensed';
  font-weight: 300;
  *vertical-align: middle; }

input[type=text],button, input[type="submit"],input[type="reset"],input[type="button"] {
  -webkit-appearance: none; }

button, input[type="submit"],input[type="reset"],input[type="button"] {
  cursor: pointer; }

b,strong {
  font-weight: 600; }

em, i {
  font-style: italic; }

a {
  color: #000;
  outline: none;
  &:hover {
    text-decoration: none; } }

input {
  &[disabled] {
    cursor: default; }
  &[type=checkbox], &[type=radio] {
    box-sizing: border-box;
    *height: 13px;
    padding: 0;
    *width: 13px; }
  &[type=search] {
    appearance: textfield;
    box-sizing: cnt-box;
    &::-webkit-search-decoration, &::-webkit-search-cancel-button {
      -webkit-appearance: none; } } }

input[type='number'] {
  -moz-appearance: textfield; }

input::-ms-clear {
  display: none;
  width : 0;
  height: 0; }

/* Base styles */
html {
  *,*:before {
    box-sizing: border-box;
    text-size-adjust: 100%;
    font-size: 100%; } }


body {
  color: #000;
  direction: ltr;
  font-family: 'RobotoCondensed', sans-serif, Arial, Helvetica, Verdana;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.2;
  overflow-y: auto; }

html, body {
  height: 100%; }

h1,.h1,h2,.h2,h3,.h3,h4,.h4 {
  text-transform: uppercase; }

h1,.h1 {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: 1px solid #000;
  font-size: 25px;
  text-align: center; }
h2,.h2 {
  padding-top: 10px;
  margin-bottom: 15px;
  font-size: 22px; }
h3,.h3 {
  padding-top: 10px;
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 400; }
h4,.h4 {
  padding-top: 10px;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700; }
h5,.h5 {
  margin-bottom: 15px;
  font-size: 16px; }

.cl {
  *zoom: 1;
  &:before,
  &:after {
    display: table;
    content: "";
    line-height: 0; }
  &:after {
    clear: both; } }

.flex-row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row; }

.flex-item {
  display: flex; }

.no-flexbox {
  .flex-row {
    display: block;
    font-size: 0; }
  .flex-item {
    display: inline-block;
    vertical-align: top; } }

.left {
  float: left; }

.right {
  float: right; }

.hide {
  display: none !important; }

.show {
  display: block !important; }

