/* Slider */

.slick-loading .slick-list {
  background: url(../images/loader.svg) center center no-repeat; }

.loader {
  display: none;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -50px 0 0 -50px;
  z-index: 2;
  background: url(../images/loader.svg) center center no-repeat; }

/* Arrows */
.slick-prev, .slick-next {
  position: absolute;
  top: 50%;
  width: 19px;
  height: 35px;
  margin-top: -17.5px;
  padding: 0;
  z-index: 2;
  cursor: pointer;
  background-image: url($spritesheet-image);
  background-repeat: no-repeat;
  background-color: transparent;
  display: none;
  font-size: 0; }

.slick-prev {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-next {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25; }

.slick-prev {
  left: 30px;
  background-position: $ico-prev-offset-x $ico-prev-offset-y; }

.slick-next {
  right: 30px;
  background-position: $ico-next-offset-x $ico-next-offset-y; }

.prod__big-img,.prod__small-img {
  .slick-prev,.slick-next {
    width: $ico-prod-prev-width;
    height: $ico-prod-prev-height;
    margin-top: -($ico-prod-prev-height/2); }
  .slick-prev {
    left: 10px;
    background-position: $ico-prod-prev-offset-x $ico-prod-prev-offset-y; }
  .slick-next {
    right: 10px;
    background-position: $ico-prod-next-offset-x $ico-prod-next-offset-y; } }

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px; }

.slick-dots {
  position: absolute;
  bottom: 22px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  font-size: 0;
  z-index: 2;
  li {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 10px;
    height: 10px;
    margin: 0 3px;
    padding: 0;
    cursor: pointer;
    div {
      position: relative;
      width: 10px;
      height: 10px;
      background-color: #fff;
      border: 2px solid #000;
      -webkit-border-radius: 50%;
      border-radius: 50%;
      cursor: pointer;
      color: transparent;
      outline: none;
      font-size: 0;
      line-height: 0; }
    &.slick-active div {
      background-color: #000;
      border-color: #fff; } } }
