/* Slider */
.brands {
  position: relative;
  background-color: #f6f6f6;
  padding: 0 30px 40px; }

.brands-carousel__item {
  padding: 0 15px;
  text-align: center;
  img {
    vertical-align: middle;
    max-height: 120px; } }

.main__category-prod {
  padding-bottom: 20px; }

.category-prod-wrp {
  position: relative; }

.category-prod__item {
  padding: 0 10px;
  text-align: center;
  span {
    display: block; } }

.category-prod__name {
  padding: 0 15px; }

.category-prod {
  font-size: 12px;
  text-decoration: none;
  color: #000;
  outline: none; }

.category-prod__img {
  margin-bottom: 12px;
  text-align: center; }

.prod__small-img {
  border-bottom: 1px solid #000;
  .slick-list {
    padding-top: 2px!important; }
  .slick-track {
    border-top: 1px solid #000; } }

.prod__small-img__item {
  text-align: center;
  cursor: pointer; }

.prod__small-img__item.slick-center {
  position: relative;
  &:before {
    content: '';
    display: block;
    position: absolute;
    border-top: 3px solid #000;
    top: -2px;
    width: 100%; } }

.prod__big-img__item {
  text-align: center; }

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
  z-index: 1;
  &:focus {
    outline: none; }
  &.dragging {
    cursor: pointer;
    cursor: hand; } }

.slick-slider {
  .slick-track, .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  &:before {
    display: table;
    content: ''; }
  &:after {
    display: table;
    content: '';
    clear: both; } }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
  outline: none;
  a {
    outline: none; } }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-slide {
  &.slick-loading img {
    display: none; }
  &.dragging img {
    pointer-events: none; } }

.slick-initialized .slick-slide {
  display: block; }

.slick-loading .slick-slide {
  visibility: hidden; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.slick-initialized .brands-carousel__item {
  display: inline-block;
  vertical-align: middle;
  float: none; }
