/*Content section*/
.table-wrp {
  overflow-y: auto; }

.cnt {
  font-size: 14px;
  font-weight: 300;
  blockquote {
    padding: 30px;
    margin-bottom: 25px;
    background-color: #f2f2f2;
    img {
      border-radius: 50%; } }
  a:not([class]) {
    color: #7b7b7b;
    &:hover {
      text-decoration: none; } }
  img {
    display: block;
    margin: 0 auto;
    float: none; }
  hr {
    border: none;
    border-bottom: 1px solid #000; }
  p {
    margin-bottom: 20px; }
  em, i {
    font-style: italic; }
  strong, b {
    font-weight: 700; }
  figure {
    clear: both;
    float: none;
    margin-bottom: 30px;
    text-align: center; }
  figcaption {
    padding-top: 10px;
    text-align: center;
    color: #5a5a5a;
    font-size: 15px; }
  ul {
    margin-bottom: 20px;
    display: inline-block; }
  ul li, ol li {
    margin-bottom: 6px;
    text-align: left; }
  ul li {
    list-style: none;
    position: relative;
    margin: 0 0 8px 16px;
    &:before {
      content: "";
      position: absolute;
      top: 9px;
      left: -15px;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: #000; } }
  ol {
    counter-reset: li;
    padding: 0;
    display: inline-block;
    margin-bottom: 20px;
    list-style: none;

    li {
      position: relative;
      margin: 0 0 8px 18px;
      &:before {
        content: counter(li)".";
        counter-increment: li;
        position: absolute;
        top: -3px;
        left: -26px;
        width: 2em;
        padding: 4px 0;
        text-align: center;
        font-weight: 700; } } }
  table {
    margin: 0 0 25px;
    border-collapse: collapse; }
  td {
    padding: 18px;
    border: 1px solid #000; }
  th {
    background-color: #f2f2f2;
    border: 1px solid #000;
    padding: 12px;
    font-weight: 400; } }
